import styles from './App.module.css';
import {useEffect, useState} from "react";
import toggleDarkJS from 'toggledarkjs';

const App = () => {
    // State variable to set the current theme mode
    const [isDarkMode, setIsDarkMode] = useState(false);

    // Calls toggleDarkJS whenever isDarkMode updates
    useEffect(() => {
        toggleDarkJS(isDarkMode, [
            {name: '--primary-color', light: 'white', dark: 'black'},
            {name: '--secondary-color', light: 'black', dark: 'white'},
        ]);
    }, [isDarkMode]);

    return (
        <div className={styles.app}>
            <h1 className={styles.title}>hello world, this is toggleDarkJS</h1>
            <button
                className={styles.actionButton}
                onClick={() => setIsDarkMode(!isDarkMode)}
            >
                Toggle theme
            </button>

            <footer className={styles.footer}>
                <p>Made with &#10084;&#65039; by
                    <a
                        className={styles.link}
                        rel={'noreferrer'}
                        href={'https://github.com/khzainulabidin'}
                        target={'_blank'}
                    > Zain Ul Abidin</a>
                </p>
            </footer>
        </div>
    );
}

export default App;
